<template>
  <div class="task-detail">
    <div class="head clearfix">
      <div class="pull-left head-left">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/task' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <div class="pull-right">
        <router-link to="/task">
          <el-button size="small"></el-button>
        </router-link>
      </div>
    </div>
    <ul class="content">
      <div class="result">
        <el-button size="small" type="primary" @click="$router.push({name: 'Result', query: {status: id}})"></el-button>
        <br>
        <br>
        <el-button size="small" type="primary" @click="$router.push({name: 'Result', query: {result: id}})"></el-button>
      </div>
      <li class="clearfix">
        <div class="left pull-left text-right"></div>
        <div class="right pull-left">
          {{data.job_status}}&nbsp;
          <el-button size="mini"  @click="changeStatus" v-show="data.enabled === 0"></el-button>
          <el-button size="mini"  @click="changeStatus" v-show="data.enabled === -1"></el-button>
          <el-button size="mini"  @click="changeStatus" v-show="data.enabled === 1" :disabled="data.enabled === 1"></el-button>
        </div>
      </li>
      <li class="clearfix">
        <div class="left pull-left text-right"></div>
        <div class="right">
          {{data.job_name}}
        </div>
      </li>
      <li class="clearfix">
        <div class="left pull-left text-right"></div>
        <div class="right">
          {{data.spider_type}}
        </div>
      </li>
      <li class="clearfix">
        <div class="left pull-left text-right"></div>
        <div class="right">
          {{data.target_web}}
        </div>
      </li>
      <li class="clearfix">
        <div class="left pull-left text-right"></div>
        <div class="right">
          {{data.spider_content}}
        </div>
      </li>
      <li class="clearfix">
        <div class="left pull-left text-right"></div>
        <div class="right">
          {{data.run_time}}
        </div>
      </li>
      <li class="clearfix">
        <div class="left pull-left text-right"></div>
        <div class="right">
          {{data.spider_freq}}
        </div>
      </li>
      <li class="clearfix">
        <div class="left pull-left text-right"></div>
        <div class="right">
          {{data.run_times}}
        </div>
      </li>
      <li class="clearfix">
        <div class="left pull-left text-right"></div>
        <div class="right">
          {{data.update_time}}
        </div>
      </li>
      <li class="clearfix">
        <div class="left pull-left text-right"></div>
        <div class="right">
          {{data.video_time}}
        </div>
      </li>
      <li class="clearfix">
        <div class="left pull-left text-right"></div>
        <div class="right">
          {{data.server}}
        </div>
      </li>
    </ul>
    <div class="clearfix foot">
      <div class="pull-left">{{data.create_time}}{{data.update_time}}{{username}}</div>
      <div class="pull-right"><router-link :to="'/task/edit/' + id"><el-button size="small"></el-button></router-link></div>
    </div>
  </div>
</template>

<script>
import axios from '@/config/http'
import qs from 'qs'
export default {
  name: 'task-detail',
  data () {
    return {
      id: this.$route.params.id,
      data: {
        create_time: '',
        enabled: '',
        job_name: '',
        job_status: '',
        run_time: '',
        run_times: '',
        server: '',
        spider_content: '',
        spider_freq: '',
        spider_type: '',
        target_web: '',
        update_time: '',
        video_time: '',
        video_upload_time: ''
      },
      username: ''
    }
  },
  methods: {
    changeStatus () {
      axios.put('/api/joblist', qs.stringify({job_id: this.id})).then(res => {
        this.$message.success('')
        this.fetchData()
      })
    },
    fetchData () {
      axios.get(`/api/joblist/${this.id}`).then(res => {
        if (res.data.code === 200) {
          this.data = res.data.rst
          this.username = res.data.user_name
        }
      })
    }
  },
  mounted () {
    this.fetchData()
  }
}
</script>

<style scoped>
.head{
  border-bottom: 1px solid #ccc;
  height: 36px;
  line-height: 36px;
  padding-bottom: 1px;
}
.head-left{
  margin-top: 13px;
  padding-bottom: 8px;
  border-bottom: 3px solid #333;
}
.content{
  position: relative;
  padding: 20px;
  border-bottom: 1px solid #ccc;
}
.result{
  position: absolute;
  top: 5px;
  right: 0;
}
.content li{
  line-height: 40px;
}
.content .left{
  width: 100px;
}
.foot{
  padding-top: 5px;
}
</style>



