<template>
  <div class="add">
    <div class="head"><div></div></div>
    <div style="padding:20px;">
      <el-form label-width="120px" size="small">
        <el-form-item label="">
          <el-input placeholder="" style="width: 300px;" v-model.trim="form.job_name"></el-input><span class="tips">20</span>
        </el-form-item>
        <el-form-item label="">
          <el-select style="width: 200px;" v-model="form.spider_type">
            <el-option value="" label=""></el-option>
            <el-option value="" label=""></el-option>
          </el-select>
        </el-form-item>
        <div v-show="form.spider_type === ''">
          <el-form-item label="">
            <el-select style="width: 200px;" v-model="form.project_id">
              <el-option v-for="item in target_webs1" :key="item.project_id" :value="item.project_id" :label="item.project_name">{{item.project_name}}</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-tag closable type="info" v-for="(item, index) in words" :key="index" @close="handleClose" :disable-transitions="false">{{item}}</el-tag>
            <el-input
              v-if="inputVisible"
              v-model="inputValue"
              ref="saveTagInput"
              size="small"
              style="width:100px"
              @keyup.enter.native="handleInputConfirm"
              @blur="handleInputConfirm"
            ></el-input>
            <el-button v-else icon="el-icon-plus" size="small" @click="showInput"></el-button>
          </el-form-item>
        </div>
        <div v-show="form.spider_type === ''">
          <el-form-item label="">
            <el-select style="width: 200px;" v-model="form.project_id" @change="targetChange">
              <el-option v-for="item in target_webs2" :key="item.project_id" :value="item.project_id" :label="item.project_name">{{item.project_name}}</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select style="width: 200px;" v-model="caijineirong">
              <el-option v-for="item in caijineirongArr" :key="item" :value="item" :label="item">{{item}}</el-option>
            </el-select>
          </el-form-item>
        </div>
        <el-form-item label="">
          <el-select style="width: 200px;" v-model="form.run_time">
            <el-option value="" label=""></el-option>
            <el-option value="" label=""></el-option>
          </el-select>
          <span v-show="form.run_time === ''">
            <el-date-picker
              v-model="form.start_date"
              format="yyyy-MM-dd"
              value-format="yyyy-MM-dd"
              type="date"
              placeholder="">
            </el-date-picker>
            
            <el-date-picker
              v-model="form.end_date"
              format="yyyy-MM-dd"
              value-format="yyyy-MM-dd"
              type="date"
              placeholder="">
            </el-date-picker>
          </span>
        </el-form-item>
        <el-form-item label="">
          <el-input placeholder="" style="width: 100px;" v-model="form.spider_freq"></el-input><span class="tips"></span>
        </el-form-item>
        <el-form-item label="">
          <el-select style="width: 200px;" v-model="form.run_type">
            <el-option value="" label=""></el-option>
            <el-option value="" label=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select style="width: 200px;" v-model="form.upload_time_type">
            <el-option value="" label=""></el-option>
            <el-option value="" label=""></el-option>
          </el-select>
          <span v-show="form.upload_time_type === ''">
            <el-date-picker
              v-model="form.upload_time_start_date"
              format="yyyy-MM-dd"
              value-format="yyyy-MM-dd"
              type="date"
              placeholder="">
            </el-date-picker>
            
            <el-date-picker
              v-model="form.upload_time_end_date"
              format="yyyy-MM-dd"
              value-format="yyyy-MM-dd"
              type="date"
              placeholder="">
            </el-date-picker>
          </span>
        </el-form-item>
        <el-form-item label="">
          <el-input placeholder="" style="width: 83px;" v-model="form.video_time_short"></el-input>
          min ~
          <el-input placeholder="" style="width: 83px;" v-model="form.video_time_long"></el-input>
          min
          <span class="tips"></span>
        </el-form-item>
        <el-form-item label="">
          <el-select style="width: 200px;" v-model="form.daemon">
            <el-option v-for="item in servers" :key="item" :value="item" :label="item === 'auto' ? '' : item">{{item === 'auto' ? '' : item}}</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select style="width: 200px;" v-model="form.pri">
            <el-option value="" label=""></el-option>
            <el-option value="" label=""></el-option>
          </el-select>
        </el-form-item>
      </el-form>
    </div>
    <div class="text-center">
      <el-button type="primary" @click="submit"></el-button>
      <el-button></el-button>
    </div>
  </div>
</template>

<script>
import qs from 'qs'
import axios from '@/config/http'
export default {
  name: 'add',
  data () {
    return {
      value1: '',
      value2: '',
      form: {
        job_name: '',
        spider_type: '',
        project_id: '',
        keywords: '',
        run_time: '',
        start_date: '',
        end_date: '',
        spider_freq: '',
        run_type: '',
        upload_time_type: '',
        upload_time_start_date: '',
        upload_time_end_date: '',
        video_time_short: '',
        video_time_long: '',
        daemon: '',
        pri: ''
      },
      target_webs1: [],
      target_webs2: [],
      servers: [],
      mubiaowangzhan: 'YouTube',
      caijineirong: '',
      caijineirongArr: [],
      words: [],
      inputVisible: false,
      inputValue: ''
    }
  },
  methods: {
    handleClose (tag) {
      this.words.splice(this.words.indexOf(tag), 1)
    },
    showInput () {
      this.inputVisible = true
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    handleInputConfirm () {
      if (this.inputValue && this.words.indexOf(this.inputValue) < 0) {
        this.words.push(this.inputValue)
      }
      this.inputVisible = false
      this.inputValue = ''
    },
    targetChange (id) {
      this.target_webs2.forEach(item => {
        if (item.project_id === id) {
          this.caijineirongArr = item.plate_name
        }
      })
    },
    getOptions () {
      axios.get('/api/project/add_jobs').then(res => {
        if (res.data.code === 200) {
          this.servers = res.data.rst.servers
          this.servers.unshift('auto')
          this.target_webs1 = res.data.rst.spider_type[0][''].target_web
          this.target_webs2 = res.data.rst.spider_type[1][''].target_web
        }
      })
    },
    submit () {
      axios.post('/api/project/add_jobs', qs.stringify(this.form)).then(res => {
        if (res.data.code === 200) {
          this.$message.success(res.data.rst)
          this.form = {
            job_name: '',
            spider_type: '',
            project_id: '',
            keywords: '',
            run_time: '',
            start_date: '',
            end_date: '',
            spider_freq: '',
            run_type: '',
            upload_time_type: '',
            upload_time_start_date: '',
            upload_time_end_date: '',
            video_time_short: '',
            video_time_long: '',
            daemon: '',
            pri: ''
          }
        }
      })
    }
  },
  mounted () {
    this.getOptions()
  },
  watch: {
    words (val) {
      this.form.keywords = val.toString()
    },
    caijineirong (val) {
      this.form.keywords = val
    }
  }
}
</script>

<style scoped>
.head{
  border-bottom: 1px solid #ccc;
  padding-bottom: 1px;
  height: 30px;
  line-height: 30px;
}
.head div{
  display: inline-block;
  padding: 0 5px;
  border-bottom: 3px solid #333;
}
.tips{
  color: #999;
  margin-left: 5px;
}
.add .el-tag{
  margin: 0 3px 3px 0;
}
</style>



